// 添加一个vue的原型属性
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

Vue.config.productionTip = false;

// 过滤器
Vue.filter('formatDate',(value,format)=>{

  // // console.log("value ==> ",value)
  // // console.log("format ==> ",format)

  let date = new Date(value);

  // if(/(y+)/.test(format)){
  //   let content = RegExp.$1;
  //   // console.log(content);
  //   format = format.replace(content,year)
  // }

  let o = {
    y: date.getFullYear(),
    M: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    m: date.getMinutes(),
    s: date.getSeconds()
  }

  for(let key in o){
    // 创建动态正则
    let regexp = new RegExp(`(${key}+)`);
    // // console.log(regexp)
    
    if(regexp.test(format)){
      let content = RegExp.$1;
      // 补零
      if(o[key] < 10 && content.length == 2){
        o[key] = '0' + o[key];
      }
      // // console.log(key,o[key].toString().length,content.length,o[key].toString().length - content.length);
      format = format.replace(content,o[key].toString().slice(o[key].toString().length - content.length));
    };
  }

  // // console.log("format ==> ",format)

  return format;
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
